Buttons
*********************************/
.blue-btn {
  background-color: #2980b9; }
  .blue-btn:hover, .blue-btn:focus {
    background-color: #2574a8; }
  .blue-btn:active {
    background-color: #2472a4; }

.btn {
  display: inline-block;
  position: relative;
  padding: 10px 20px;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1em; }
  .btn:before {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 90%;
    top: 50%;
    display: block;
    content: "";
    border-bottom: solid 3px #333333;
    border-left: solid 3px #333333;
    animation: btn-hover-before-back .6s;
    animation-fill-mode: forwards; }
  .btn:after {
    position: absolute;
    bottom: 50%;
    left: 90%;
    right: 0;
    top: 0;
    display: block;
    content: "";
    border-top: solid 3px #333333;
    border-right: solid 3px #333333;
    animation: btn-hover-after-back .6s;
    animation-fill-mode: forwards; }
  .btn:hover:before, .btn:hover:after, .btn:focus:before, .btn:focus:after {
    opacity: 1; }
  .btn:hover:before, .btn:focus:before {
    animation: btn-hover-before .6s;
    animation-fill-mode: forwards; }
  .btn:hover:after, .btn:focus:after {
    animation: btn-hover-after .6s;
    animation-fill-mode: forwards; }

@keyframes btn-hover-before {
  0% {
    bottom: 0;
    left: 0;
    right: 90%;
    top: 50%; }
  50% {
    bottom: 0;
    left: 0;
    top: calc(100% - 3px);
    right: 0;
    border-left: solid 3px #333333;
    border-right: none; }
  51% {
    border-left: none;
    border-right: solid 3px #333333; }
  100% {
    bottom: 0;
    top: 60%;
    left: 91%;
    right: 0;
    border-left: none;
    border-right: solid 3px #333333; } }

@keyframes btn-hover-before-back {
  0% {
    bottom: 0;
    top: 60%;
    left: 91%;
    right: 0;
    border-left: none;
    border-right: solid 3px #333333; }
  50% {
    border-left: none;
    border-right: solid 3px #333333; }
  51% {
    bottom: 0;
    left: 0;
    top: calc(100% - 3px);
    right: 0;
    border-left: solid 3px #333333;
    border-right: none; }
  100% {
    bottom: 0;
    left: 0;
    right: 90%;
    top: 50%; } }

@keyframes btn-hover-after {
  0% {
    bottom: 50%;
    left: 90%;
    right: 0;
    top: 0; }
  50% {
    bottom: calc(100% - 3px);
    left: 0;
    top: 0;
    right: 0;
    border-right: solid 3px #333333;
    border-left: none; }
  51% {
    border-right: none;
    border-left: solid 3px #333333; }
  100% {
    bottom: 60%;
    top: 0;
    left: 0;
    right: 92%;
    border-right: none;
    border-left: solid 3px #333333; } }

@keyframes btn-hover-after-back {
  0% {
    bottom: 60%;
    top: 0;
    left: 0;
    right: 92%;
    border-right: none;
    border-left: solid 3px #333333; }
  50% {
    border-right: none;
    border-left: solid 3px #333333; }
  51% {
    bottom: calc(100% - 3px);
    left: 0;
    top: 0;
    right: 0;
    border-right: solid 3px #333333;
    border-left: none; }
  100% {
    bottom: 50%;
    left: 90%;
    right: 0;
